import { Center, Flex, Input, InputGroup } from "@chakra-ui/react";
import { useState } from "react";
import { useAppSelector } from "src/hooks/useStore";
import { globalSelector } from "../../store/global";

export default function Report() {
  const { searchKey } = useAppSelector(globalSelector);
  const [search, setSearch] = useState<string>(searchKey);

  return (
    <Flex direction="column">
      <Center color="primary.200" fontWeight="semibold">Report this website</Center>
      <InputGroup>
        <Input
          background="#F7F7F7"
          value={search}
          onChange={(e) => {
            setSearch(e.target.value);
          }}
          mx={4}
          mt={4}
        />
      </InputGroup>
    </Flex>
  );
}
